.container {
    justify-content: center;
    margin: 50px auto auto;
    max-width: 100%;
}

.rounded {
    max-height: 875px;
    max-width: 550px;
    min-width: 490px;
}

.badge {
    cursor: pointer;
    transition: all .2s ease;
}

.badge:hover {
    transform: scale(1.2);
    transition: all .2s ease;
}

.checked {
    color: orange;
}

.fa {
    cursor: pointer;
    transition: all .2s ease;
}

hr {
    height: 1px;
}

iframe {
    width: 450px;
    height: 250px;
}

.trailer {
    margin-top: 10px;
}

.fa:hover {
    transform: scale(1.5);
    transition: all .2s ease;
}

@media (max-width: 1500px) {
    iframe {
        width: 400px;
        height: 220px;
    }
    img{
        margin: unset;
    }
   
}

@media (max-width: 1300px) {
    iframe {
        width: 350px;
        height: 190px;
    }

}