.card {
    width: 300px;
    min-width: 300px;
}

@media (min-width: 908px) {
    .card {
        min-width: 350px !important;
        transition: all .4s ease;
    }
}
@media (max-width: 700px) {
    .card {
        min-width: 400px !important;
        transition: all .4s ease;
    }
}